// Libraries
import React, { useEffect, useRef, useState } from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { Link, useLocation } from "react-router-dom";
import Offcanvas from "react-bootstrap/Offcanvas";

// Components

// Styles & Icons
import HomeIcon from "@mui/icons-material/Home";
import "./Header.scss";
import MenuIcon from "@mui/icons-material/Menu";
import Searchbar from "./Searchbar";
import SearchIcon from "@mui/icons-material/Search";

// Nav Items
const sidebarNavItems = [
  {
    display: "Home",
    to: "/",
    section: "home",
  },
  {
    display: "Desarrollos",
    to: "/desarrollos",
    section: "desarrollos",
  },
  {
    display: "Constructora",
    to: "/constructora",
    section: "constructora",
  },
  {
    display: "Prensa",
    to: "/prensa",
    section: "prensa",
  },
];

const Header = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [stepWidth, setStepWidth] = useState(0);
  const navRef = useRef();
  const indicatorRef = useRef();
  const location = useLocation();
  const homeRef = useRef();

  // X
  // if (homeRef != undefined) {
  //   const homeX = homeRef.current.offsetLeft;
  //   console.log(homeX);
  // }

  useEffect(() => {
    const navItem = navRef.current.querySelector(".nav-item-p");
    switch (activeIndex) {
      case 0:
        setStepWidth(53);
        break;

      case 1:
        setStepWidth(55);
        break;

      case 2:
        setStepWidth(81);
        break;

      case 3:
        setStepWidth(94);
        break;

      default:
        break;
    }
    if (activeIndex === 0) {
    }
    console.log(navItem.clientHeight);
  }, [activeIndex]);

  //cambiar index activado
  useEffect(() => {
    const curPath = window.location.pathname.split("/")[1];
    const activeItem = sidebarNavItems.findIndex(
      (item) => item.section === curPath
    );
    setActiveIndex(curPath.length === 0 ? 0 : activeItem);
  }, [location]);

  return (
    <>
      <Navbar
        collapseOnSelect
        expand="lg"
        bg="#014751"
        variant="dark"
        sticky="top"
        className="nav-p"
      >
        <Container fluid className="header-container">
          <Navbar.Brand>
            <Link to="/">
              <img
                src="https://i.imgur.com/PVHkzh6.png"
                className="brand-img d-inline-block align-top"
                alt="Basa Logo"
              />
            </Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <SearchIcon sx={{ fontSize: 40 }} />
            <Nav className="mt-1">
              <Searchbar />
            </Nav>
            <Nav className="me-auto"></Nav>
            <Nav ref={navRef}>
              <div
                ref={indicatorRef}
                id="marker"
                style={{
                  transform: `translateX(${
                    activeIndex * stepWidth
                  }px) translateY(50%)`,
                }}
              ></div>

              {sidebarNavItems.map((item, index) => (
                <Nav.Link
                  className={`nav-item nav-item-p ${index === 0 && "__home"}`}
                  to="/"
                >
                  <Link
                    to={item.to}
                    key={index}
                    className={`_link-nav ${
                      activeIndex === index ? "active" : ""
                    }`}
                  >
                    {index === 0 ? (
                      <HomeIcon sx={{ fontSize: 30 }} />
                    ) : (
                      item.display
                    )}
                  </Link>
                </Nav.Link>
              ))}
              {/* <Nav.Link className="nav-item nav-item-p" to="/">
                <Link
                  to="/"
                  className={`_link-nav ${
                    activeIndex === index ? "active" : ""
                  }`}
                >
                  <HomeIcon sx={{ fontSize: 40 }} />
                </Link>
              </Nav.Link>
              <Nav.Link className="nav-item nav-item-p">
                <Link to="/desarrollos" className="_link-nav">
                  Desarrollos
                </Link>
              </Nav.Link>

              <Nav.Link className="nav-item nav-item-p">
                <Link to="/constructora" className="_link-nav">
                  Constructora
                </Link>
              </Nav.Link>
              <Nav.Link className="nav-item nav-item-p" to="/prensa">
                <Link to="/prensa" className="_link-nav">
                  Prensa
                </Link>
              </Nav.Link> */}

              <Nav.Link
                className="nav-item nav-item-p contacto-p"
                href="#contacto"
              >
                <i>Contacto</i>
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
};

export default Header;
